.home-Pager {

  &_Inner {
    width: 85%;
    margin: {
      top: 4em;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 85%;
    }

    @include for-tablet-landscape-up {
      width: 80%;
    }

    @include for-desktop-up {
      // max-width: 960px - 100px;
      max-width: 960px - 200px;
    }

  }

  &_List {
    display: flex;
    justify-content: center;
  }

  &_Item {
    margin: {
      left: 4em;
    }

    &:first-child {
      margin: {
        left: 0;
      }
    }

  }

  &_Link {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    padding: {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    color: $textLinkColor;
    font-size: 1.5rem;
    line-height: 1;
    text-decoration: none;
    text-transform: capitalize;
    transition: all $transitionSpeed;

    @include for-tablet-portrait-up {
      font-size: 1.5rem + 0.1666;
    }

    @include for-tablet-landscape-up {
      font-size: 1.5rem + 0.1666 + 0.1666;
    }

    @include for-desktop-up {
      font-size: 2rem;
    }

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 1px;
      background: {
        color: $linkColor;
      }
      opacity: 0;
      transition: all .3s;
    }

    &:hover {
      &::after {
        @include for-tablet-portrait-up {
          opacity: 1;
        }

      }

    }

  }

}