.single-Header {
  text-align: center;

  &_Title {
    width: 85%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }
    color: #000;
    font-size: 2.4rem;
    letter-spacing: .1em;
    line-height: 1.65;

    @include for-tablet-portrait-up {
      width: 85%;
      font-size: 2.4rem + 0.4;
    }

    @include for-tablet-landscape-up {
      width: 80%;
      font-size: 2.4rem + 0.4 + 0.4;
    }

    @include for-desktop-up {
      max-width: 960px - 200px;
      font-size: 3.2rem;
      letter-spacing: .014rem;
      line-height: 1.2;
    }

  }

  &_Meta {
    margin: {
      top: .7111em;
      // bottom: 1.422222em;
    }
    color: #999;
    font-size: 1.125rem;
    line-height: 1.4;
    letter-spacing: .02em;

    // @include for-tablet-portrait-up {
    // }

    // @include for-tablet-landscape-up {
    // }

    @include for-desktop-up {
      margin: {
        top: 16px;
        // bottom: 24px;
      }
      font-size: 1.5rem;
    }

  }

  &_MetaLower {
    margin: {
      top: .6125em;
    }
    font-size: 1.3rem;

    // @include for-tablet-portrait-up {

    // }

    // @include for-tablet-landscape-up {

    // }

    @include for-desktop-up {
      font-size: 1.5rem;
      margin: {
        top: 8px;
      }

    }

  }

  &_MetaLinks {
    color: $textLinkColor;
    letter-spacing: .14em;
    text-transform: uppercase;
    text-decoration: none;
    vertical-align: middle;
    transition: opacity $transitionSpeed;

    &:hover {
      @include for-tablet-portrait-up {
        opacity: .7;
      }

    }

  }

  &_MetaCategory {
    &::before {
      margin: {
        right: .8em;
      }
      content: '\f07b';
      font-family: "Font Awesome 5 Free";
      font-size: 1rem;

      @include for-desktop-up {
        margin: {
          right: 8px;
        }

      }

    }

  }

  &_MetaTags {
    margin: {
      left: .8em;
    }

    @include for-desktop-up {
      margin: {
        left: 8px;
      }

    }

    &::before {
      margin: {
        right: .8em;
      }
      content: '\f02c';
      font-family: "Font Awesome 5 Free";
      font-size: 1rem;

      @include for-desktop-up {
        margin: {
          right: 8px;
        }

      }

    }

    & + & {
      margin: {
        left: 0;
      }
    }

    & + &::before {
      content: ',';
    }

  }

}