.common-GlobalNav {
  margin: {
    bottom: 8em;
  }

  &_Inner {
    width: 95%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }
    padding: {
      top: 1.5em;
      right: 0;
      bottom: 1.0em;
      left: 0;
    }

    @include for-tablet-landscape-up {
      width: 98%;
    }

  }

  &_List {
    display: flex;

    flex-wrap: wrap;
    justify-content: center;

    @include for-tablet-portrait-up {
      flex-wrap: nowrap;
      justify-content: flex-end;
    }

  }

  &_Items {
    margin: {
      left: 1.5em;
    }
    font-size: 1.25rem;
    letter-spacing: .1em;


    @include for-tablet-portrait-up {
      font-size: 1.25rem + 0.11666;
    }

    @include for-tablet-landscape-up {
      font-size: 1.25rem + 0.11666 + 0.11666;
    }

    @include for-desktop-up {
      font-size: 1.6rem;

    }

    &:first-child {
      margin: {
        left: 0;
        bottom: .8em;
      }

      @include for-tablet-portrait-up {
        margin: {
          bottom: 0;
        }
      }

    }

    &:last-child {
      margin: {
        left: 0;
      }

      @media screen and (min-width: 320px) {
         margin: {
          top: 0;
          left: 1.5em;
        }

      }


    }

  }

  &_Link {
    position: relative;
    color: $textLinkColor;
    text-decoration: none;
    transition: opacity $transitionSpeed;

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 1px;
      background: {
        color: $linkColor;
      }
      opacity: 0;
      transition: all .3s
    }

    &:hover {
      @include for-tablet-portrait-up {
        opacity: .8;
      }

      &::after {
        @include for-tablet-portrait-up {
          opacity: 1;
        }

      }

    }

  }

}