.home-Content {
  margin: {
    top: 4.8em;
  }

  &_Inner {
    width: 85%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 85%;
    }

    @include for-tablet-landscape-up {
      width: 80%;
    }

    @include for-desktop-up {
      // max-width: 960px - 100px;
      max-width: 960px - 200px;
    }

  }

  &_PostItem {
    margin: {
      top: 3.2em;
    }
    padding: {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    cursor: pointer;
    transition: all .2s;

    @include for-tablet-portrait-up {
      margin: {
        top: 1.6em;
      }

    }

    &:first-child {
      margin: {
        top: 0;
      }

    }

  }

  &_PostLink {
    display: block;
    width: 100%;
    height: 100%;
    color: $textDefaultColor;
    text-decoration: none;

    @include for-tablet-portrait-up {
      padding: {
        top: 1.6em;
        right: 2.4em;
        bottom: 1.6em;
        left: 2.4em;
      }

    }

  }

  &_PostDate {
    display: block;
    color: #777;
    font-size: 1.125rem;
    letter-spacing: .02em;
    text-transform: uppercase;
    opacity: .8;

    @include for-tablet-portrait-up {
      font-size: 1.125rem + 0.125;
    }

    @include for-tablet-landscape-up {
      font-size: 1.125rem + 0.125 + 0.125;
    }

    @include for-desktop-up {
      font-size: 1.5rem;
    }

  }

  &_PostTitle {
    display: block;
    margin: {
      top: .4444em;
    }
    color: #444;
    font-size: 1.8rem;
    font-weight: bold;
    letter-spacing: .1em;
    line-height: 1.25;
    transition: color $transitionSpeed;

    @include for-tablet-portrait-up {
      margin: {
        top: .4em;
      }
      font-size: 1.8rem + 0.2;
    }

    @include for-tablet-landscape-up {
      margin: {
        top: .3636em;
      }
      font-size: 1.8rem + 0.2 + 0.2;
    }

    @include for-desktop-up {
      margin: {
        top: .333em;
      }
      font-size: 2.4rem;
    }

  }

  &_PostDescription {
    display: block;
    margin: {
      top: .7111em;
    }
    color: #777;
    font-size: 1.125rem;
    letter-spacing: .05em;
    line-height: 1.6;
    text-align: justify;

    @include for-tablet-portrait-up {
      margin: {
        top: .64em;
      }
      font-size: 1.125rem + 0.125;
    }

    @include for-tablet-landscape-up {
      margin: {
        top: .8727em;
      }
      font-size: 1.125rem + 0.125 + 0.125;
    }

    @include for-desktop-up {
      margin: {
        top: .8em;
      }
      font-size: 1.5rem;
    }

  }

  &_PostMeta {
    margin: {
      top: .7272em;
    }
    font-size: 1.2rem;
  }

  &_PostCategory {
    margin: {
      right: 1.333em;
    }
    text-transform: uppercase;

    &::before {
      margin: {
        right: .2em;
      }
      content: '\f07b';
      color: #999;
      font-family: "Font Awesome 5 Free";
      font-size: 1rem;

      @include for-desktop-up {
        margin: {
          right: 4px;
        }

      }

    }

  }

  &_PostTag {
    text-transform: uppercase;

    &::before {
      margin: {
        right: .2em;
      }
      content: '\f02c';
      color: #999;
      font-family: "Font Awesome 5 Free";
      font-size: 1rem;

      @include for-desktop-up {
        margin: {
          right: 4px;
        }

      }

    }

    & + &:before {
      content: ',';
    }

  }


  // --------------
  // Link-Hover
  // --------------
  &_PostItem:hover {
    @include for-tablet-portrait-up {
      border-radius: 4px;
      box-shadow:0px 1px 8px 4px #e7e7e7;
    }

    .home-Content_PostTitle {
      @include for-tablet-portrait-up {
        color: $textLinkColor;
      }

    }

  }

}