* {
  box-sizing: border-box;
}

ul li,
ol li {
  list-style-type: none;
}

html, body {
  color: #333;
  font-family: -apple-system, BlinkMacSystemFont, 'YakuHanJP', 'MyHiragino', 'メイリオ', Meiryo, Osaka, 'MS UI Gothic', 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  font-size: 62.5%;
  -webkit-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

h1,
h2,
h3 {
  font-weight: normal;
}