.tags-Content {
  margin: {
    top: 4.8em;
  }

  &_Inner {
    width: 90%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 85%;
    }

    @include for-tablet-landscape-up {
      width: 80%;
    }

    @include for-desktop-up {
      // max-width: 960px - 100px;
      max-width: 960px - 200px;
    }

  }

  &_PostList {
    text-align: center;

    // @include for-tablet-portrait-up {}

    // @include for-tablet-landscape-up {}

    // @include for-desktop-up {}

  }

  &_PostItem {
    display: inline-block;
    margin: {
      bottom: 2em;
      left: 2em;
    }

    // @include for-tablet-portrait-up {}

    // @include for-tablet-landscape-up {}

    // @include for-desktop-up {}

    &:first-child {
      margin: {
        top: 0;
        left: 0;
      }
    }

  }

  &_PostLink {
    position: relative;
    color: $textLinkColor;
    font-size: 1.5rem;
    line-height: 1;
    text-decoration: none;
    text-transform: uppercase;
    transition: all $transitionSpeed;

    @include for-tablet-portrait-up {
      font-size: 1.5rem + 0.1666;
    }

    @include for-tablet-landscape-up {
      font-size: 1.5rem + 0.1666 + 0.16666;
    }

    @include for-desktop-up {
      font-size: 2rem;
    }

    &::before {
      position: absolute;
      bottom: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 1px;
      background: {
        color: $linkColor;
      }
      opacity: 0;
      transition: all .3s
    }

    &::after {
      content: ','
    }

    &:hover {
      @include for-tablet-portrait-up {
        opacity: .7;
      }

      &::before {
        @include for-tablet-portrait-up {
          opacity: 1;
        }

      }

    }

  }

  &_PostItem:last-child &_PostLink::after {
    display: none;
  }

}