.tag-Content {
  margin: {
    top: 4.8em;
  }

  &_Inner {
    width: 70%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 60%;
    }

    @include for-tablet-landscape-up {
      width: 50%;
    }

    @include for-desktop-up {
      max-width: 560px;
    }

  }

  &_PostItem {
    margin: {
      top: 3.2em;
    }
    padding: {
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
    cursor: pointer;
    transition: all .3s;

    &:first-child {
      margin: {
        top: 0;
      }

    }

  }

  &_PostLink {
    display: block;
    width: 100%;
    height: 100%;
    color: $textDefaultColor;
    text-decoration: none;

  }

  &_PostDate {
    color: #777;
    font-size: 1.125rem;
    letter-spacing: .02em;
    text-transform: uppercase;
    opacity: .8;

    @include for-tablet-portrait-up {
      font-size: 1.125rem + 0.125;
    }

    @include for-tablet-landscape-up {
      font-size: 1.125rem + 0.125 + 0.125;
    }

    @include for-desktop-up {
      font-size: 1.5rem;
    }

  }

  &_PostTitle {
    display: block;
    margin: {
      top: .5333em;
    }
    color: #444;
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: .1em;
    line-height: 1.25;
    transition: color $transitionSpeed;

    @include for-tablet-portrait-up {
      margin: {
        top: .48em;
      }
      font-size: 1.5rem + 0.1666;
    }

    @include for-tablet-landscape-up {
      margin: {
        top: .43639em;
      }
      font-size: 1.5rem + 0.1666 + 0.1666;
    }

    @include for-desktop-up {
      margin: {
        top: .4em;
      }
      font-size: 2.0rem;
    }

  }

  &_PostMeta {
    margin: {
      top: .7272em;
    }
    font-size: 1.2rem;
  }

  &_PostCategory {
    margin: {
      right: .8em;
    }
    text-transform: uppercase;

    &::before {
      margin: {
        right: .2em;
      }
      content: '\f07b';
      color: #999;
      font-family: "Font Awesome 5 Free";
      font-size: 1rem;

      @include for-desktop-up {
        margin: {
          right: 4px;
        }

      }

    }

  }

  &_PostTag {
    text-transform: uppercase;

    &::before {
      margin: {
        right: .2em;
      }
      content: '\f02c';
      color: #999;
      font-family: "Font Awesome 5 Free";
      font-size: 1rem;

      @include for-desktop-up {
        margin: {
          right: 4px;
        }

      }

    }

  }

  // --------------
  // Link-Hover
  // --------------
  &_PostItem:hover {
    .tag-Content_PostTitle {
      @include for-tablet-portrait-up {
        color: $textLinkColor;
      }

    }

  }

}