.common-GlobalFooter {
  margin: {
    top: 9.6em - 1.2;
    bottom: 2em;
  }
  text-align: center;

  @at-root .tags #{&} {
    margin: {
      top: 9.6em - 1.2 - 2;
    }
  }

  &_Inner {
    width: 90%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 85%;
    }

    @include for-tablet-landscape-up {
      width: 80%;
    }

    @include for-desktop-up {
      max-width: 960px - 100px;
    }
  }

  &_SocialList {
    display: flex;
    justify-content: center;
    margin: {
      bottom: 1.6em;
    }

  }

  &_SocialItem {
    margin: {
      left: 2em;
    }

    // @include for-tablet-portrait-up {
    // }

    // @include for-tablet-landscape-up {
    // }

    // @include for-desktop-up {
    // }

    &:first-child {
      margin: {
        left: 0;
      }
    }

  }

  .symbol {
    color: #555;
    font-size: 2.4rem;
    text-decoration: none;
    transition: color $transitionSpeed;

    @include for-tablet-portrait-up {
    }

    @include for-tablet-landscape-up {
    }

    @include for-desktop-up {
      font-size: 3.2rem;
    }

    &:hover {
      @include for-tablet-landscape-up {
        color: $textLinkColor;
      }
    }

  }

  &_Copyright {
    font-size: 1.2rem;
    text-transform: lowercase;

    // @include for-tablet-portrait-up {
    // }

    // @include for-tablet-landscape-up {
    // }

    @include for-desktop-up {
      font-size: 1.2rem;
    }

  }

}