.single-Entry {
  position: relative;
  width: 100%;
  margin: {
    top: 2.8em;
  }
  color: $textDefaultColor;
  font-size: 1.6rem;
  letter-spacing: .06em;
  line-height: 1.7;
  word-break: break-all;
  text-align: justify;

  @include for-tablet-portrait-up {
    font-size: 1.6rem + 0.2;
    line-height: 2;
    text-align: left;
  }

  @include for-tablet-landscape-up {
    font-size: 1.6rem + 0.2 + 0.2;
  }

  @include for-desktop-up {
    font-size: 2.0rem;
  }

  & > * {
    width: 90%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }

    @include for-tablet-portrait-up {
      width: 85%;
    }

    @include for-tablet-landscape-up {
      width: 80%;
    }

    @include for-desktop-up {
      max-width: 960px - 200px;
    }

  }

  h1, h2, h3, h4, h5, h6 {
    // color: #000;
  }

  h2 {
    font-weight: bold;
    font-size: 1.8rem;

    @include for-tablet-portrait-up {
      font-size: 1.8rem + 0.3;
    }

    @include for-tablet-landscape-up {
      font-size: 1.8rem + 0.3 + 0.3;
    }

    @include for-desktop-up {
      font-size: 2.4rem;
      margin: {
        top: 40px;
      }

    }

  }

  h3 {
    font-weight: bold;
    margin: {
      top: 1.8em;
    }
    // font-size: 1.7rem;

    // @include for-tablet-portrait-up {
    //   font-size: 1.7rem + 0.3;
    // }

    // @include for-tablet-landscape-up {
    //   font-size: 1.7rem + 0.3 + 0.3;
    // }

    // @include for-desktop-up {
    //   font-size: 2.3rem;
    // }

  }

  h2 + h3 {
    margin: {
      top: 0.4em;
    }

  }

  & > p,
  & > pre {
    margin: {
      bottom: 1.5em;
    }

    @include for-desktop-up {
      margin: {
        bottom: 1.6em;
      }

    }

  }

  & > *:last-child {
    margin: {
      bottom: 0;
    }

  }

  a {
    color: $textLinkColor;
    text-decoration: none;
    transition: opacity $transitionSpeed;

    &:hover {
      @include for-tablet-portrait-up {
        opacity: .7;
      }

    }

  }

  &_Ytube {
    margin: {
      bottom: 1.5em;
    }
    @include for-desktop-up {
      margin: {
        bottom: 24px;
      }
    }
  }

  &_YtubeInner {
    position:relative;
    width: 100%;
    margin: {
      top: 0;
      right: auto;
      bottom: 0;
      left: auto;
    }
    padding: {
      top: 56.25%;
    }
    text-align: center;

    iframe {
      position:absolute;
      top:0;
      right:0;
      width:100%;
      height:100%;
    }

  }

  ol,
  ul {
    padding: {
      left: 1.3em;
    }
  }

  ol li,
  ul li {
    list-style-type: inherit;
  }


}