.list-Header {
  width: 90%;
  margin: {
    top: 0;
    right: auto;
    bottom: 0;
    left: auto;
  }
  text-align: center;

  @include for-tablet-portrait-up {
    width: 85%;
  }

  @include for-tablet-landscape-up {
    width: 80%;
  }

  @include for-desktop-up {
    max-width: 960px - 100px;
  }

  &_SiteImage {
    overflow: hidden;
    display: block;
    width: 6em;
    height: 6em;
    margin: {
      top: 0;
      right: auto;
      bottom: 2.4em;
      left: auto;
    }
    border-radius: 50%;

    @include for-tablet-portrait-up {
      width: 6em + 0.6666;
      height: 6em + 0.6666;
    }

    @include for-tablet-landscape-up  {
      width: 6em + 0.6666 + 0.6666;
      height: 6em + 0.6666 + 0.6666;
    }

    @include for-desktop-up {
      width: 8em;
      height: 8em;
    }

    img {
      width: 100%;
      height: 100%;
    }

  }

  &_Title {
    color: #555;
    font-size: 1.8rem;
    letter-spacing: .12em;
    line-height: 1.18;
    text-transform: lowercase;

    @include for-tablet-portrait-up {
      font-size: 1.8rem + 0.3;
    }

    @include for-tablet-landscape-up  {
      font-size: 1.8rem + 0.3 + 0.3;
    }

    @include for-desktop-up {
      font-size: 2.4rem;
    }

  }

}